.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  background-color: deepskyblue;
  font-family: sans-serif;
}
.user {
  display: inline-block;
  background-color: ghostwhite;
  border-radius: 5px;
  padding: 10px 10px 10px 0;
  margin: 2px;
}

.gold, .silver, .bronze {
  padding: 3px;
  border-style: solid;
  border-width: 1px;
}

.gold {
  color: #FFCB00;
  background-color: #FFF8E1;
  border-color: #FFCB00;
}

.silver {
  color: #B3B7BB;
  border-color: #B3B7BB;
}

.bronze {
  color: #D0A583;
  border-color: #D0A583;
}

.medals {
  margin-top: 3px;
  -ms-flex-line-pack: center;
      align-content: center;
  margin: 0 auto;
}

.profile-pic {
  height: 60px;
  width: 60px;   
}

.rank {
  font-size: 15px;
}

h3 {
  text-align: center;
  margin: 0 auto;
}

.list {
  list-style: none;
}

nav {
  margin: 0 auto; 
}




